import { useEffect, useRef, useState } from 'react'
import './Chat.css'
import { conectarWebSocket, desconectarWebSocket, enviarMensajeWebsocket, suscribirAlCanalWebSocket } from './Servicios/mensajes'
import { useDispatch, useSelector } from 'react-redux';
import { useLocalStorage } from './Hooks/useLocalStore';
import { useNavigate } from 'react-router';
import { clearToken, getToken } from './Servicios/token';
import { validarTokenUsuario } from './Servicios/usuarios';
import { accionLogin, accionUsuarioLogueado } from '../state/actions';


export const Chat = props => {
    const { titulo } = props

    const [ mensajes, setMensajes ] = useState([/* 
        { autor: 'Pepe', texto: 'Hola que tal!' },
        { autor: 'Juan', texto: 'Muy bien' },
        { autor: 'Ana', texto: 'Bárbaro!' },
     */])

    //const [usuario, setUsuario] = useState('')
    const [texto, setTexto] = useState('')

    const refTexto = useRef()

    const usuarioLogueado = useSelector(state => state.usuarioLogueado)
    const { usuario, admin } = usuarioLogueado

    const dispatch = useDispatch()
    const navigate = useNavigate()
    const[ ,SetNav] = useLocalStorage('navigator', '/')

    useEffect(() => {
        console.log('-- Chat montado --')

        async function validar() {
            const token = getToken()
            //console.log(token)

            const rta = await validarTokenUsuario(token)
            //console.log(rta)

            if (rta.error) {
                dispatch(accionLogin(false))
                dispatch(accionUsuarioLogueado('', false))
                clearToken()
                navigate('/')
            }
            else {
                SetNav('/chat')                
            }
        }
        validar()
    }, [dispatch, navigate, SetNav])

    useEffect(() => {
        conectarWebSocket()
        suscribirAlCanalWebSocket('mensajes', mensajes => {
            console.log('mensajes: ', mensajes)
            setMensajes(mensajes)
        })

        return () => {
            desconectarWebSocket()
        }
     },[])

    const onSubmit = e => {
        e.preventDefault()

        console.log('usuario:', usuario)
        console.log('texto:', texto)

        const mensaje = { autor: usuario, texto, admin, fyh: new Date().toLocaleString() }
        //setMensajes([...mensajes, mensaje])
        enviarMensajeWebsocket('nuevo-mensaje', mensaje)

        refTexto.current.focus()
        setTexto('')
    }

    return (
        <div className="Chat">
            <div className="jumbotron">
                <h3>{ titulo }</h3>
                <hr />

                <div>
                    {
                        mensajes.filter(m => (m.autor === usuario) || m.admin || admin).map((mensaje, index) => 
                            <div key={index}>
                                <b>[{mensaje.fyh}] { mensaje.autor=== usuario?'Yo':mensaje.autor }: </b>
                                <i>{ mensaje.texto }</i>
                            </div>
                        )
                    }
                </div>

                <form className='form-inline' onSubmit={onSubmit}>
                    <input type="text" className='form-control w-25 mr-2 text-muted' readOnly value={usuario} />
                    <input type="text" className='form-control w-25 mr-3' placeholder='ingrese mensaje' value={texto} ref={refTexto} onChange={
                        e => setTexto(e.target.value)
                    } />
                    <input type="submit" className='btn btn-success my-4' value="Enviar" />
                </form>
            </div>
        </div>

    )
}