import { useEffect } from 'react'

import './Index.css'
import { useDispatch } from 'react-redux'
import { useNavigate } from 'react-router'
import { clearToken, getToken } from '../Servicios/token'
import { validarTokenUsuario } from '../Servicios/usuarios'
import { accionLogin, accionUsuarioLogueado } from '../../state/actions'
import { useLocalStorage } from '../Hooks/useLocalStore'

export function Index(props) {
    const { titulo: enunciado } = props             // destructuring Object con alias

    const dispatch = useDispatch()
    const navigate = useNavigate()
    const[ ,SetNav] = useLocalStorage('navigator', '/')

    useEffect(() => {
        console.log('-- Contacto montado --')

        async function validar() {
            const token = getToken()
            //console.log(token)

            const rta = await validarTokenUsuario(token)
            //console.log(rta)

            if (rta.error) {
                dispatch(accionLogin(false))
                dispatch(accionUsuarioLogueado('', false))
                clearToken()
                navigate('/')
            }
            else {
                SetNav('/contacto')                
            }
        }
        validar()
    }, [dispatch, navigate, SetNav])

    useEffect(() => {
        //console.log('Componente Index Contacto (montado)')
        
        return () => {
            //console.log('Componente Index Contacto (desmontado)')
        }
    },[])

    return (
        <div className="Contacto">
            <div className="jumbotron">
                <h3>Componente {enunciado}</h3>
                <hr />


            </div>
        </div>
    )
}