import { io } from 'socket.io-client'

const URL_API_MENSAJES = process.env.NODE_ENV === 'production'
                                    ? '/'
                                    : `http://localhost:${process.env.REACT_APP_PORT_SRV_DEV}/`


let socket

export const conectarWebSocket = () => {
    socket = io(URL_API_MENSAJES)
    console.log(`Conectando a WebSocket... OK`, socket)
}

export const suscribirAlCanalWebSocket = (canal, cb) => {
    if(!socket) {
        console.log('socket no inicializado!')
        return null
    }
    socket.on(canal,cb)
}

export const enviarMensajeWebsocket = (canal, mensaje) => {
    if(socket) socket.emit(canal, mensaje)
}

export const desconectarWebSocket = () => {
    if(socket) {
        console.log(`Desconectando a WebSocket... OK`)
        socket.disconnect()
    }
}
